/* Estrutura geral */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f9;
  color: #333;
}

.App {
  text-align: center;
}

/* Header */
.header {
  background-color: #333;
  color: white;
  padding: 1rem;
}

.header h1 {
  margin: 0;
}

.nav button {
  background: none;
  border: none;
  color: white;
  margin: 0 1rem;
  cursor: pointer;
  font-size: 1rem;
}

/* About Section */
.about-section {
  padding: 2rem;
  background-color: #fff;
  color: #333;
}

.about-section h2 {
  font-size: 2rem;
  color: #0077cc;
}

/* Projects Section */
.projects-section {
  padding: 2rem;
  background-color: #f4f4f9;
}

.project-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
}

.project-card {
  background-color: white;
  padding: 1rem;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.project-card h3 {
  margin: 0;
  color: #0077cc;
}

.project-link,
.gh-link {
  display: inline-block;
  margin-top: 1rem;
  color: #0077cc;
  text-decoration: none;
}

/* Contact Section */
.contact-section {
  padding: 2rem;
  background-color: #333;
  color: white;
}

.contact-section h2 {
  color: #0077cc;
}

.social-links a {
  color: #0077cc;
  margin: 0 0.5rem;
  text-decoration: none;
}
